import React, { Component } from 'react';
import { Route, Switch, NavLink } from 'react-router-dom';
import About from './About.js';
import Work from './Work.js';
import Contact from './Contact.js';
import './App.css';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {}
  }

  render() {
    return (
      <div className="App">
        <nav className="nav-bar">
          <NavLink exact activeClassName="active-link" to='/' className="link">About</NavLink>
          <NavLink exact activeClassName="active-link" to='/work' className="link">Work</NavLink>
          <NavLink exact activeClassName="active-link" to='contact' className="link">Contact</NavLink>
        </nav>
        <div className="wrapper">
        <Switch>
          <Route exact path="/" component={About} />
          <Route exact path="/work" render={() => <Work name="Something really cool" />} />
          <Route exact path="/contact" component={Contact} />
        </Switch>
        </div>
      </div>
    );
  }
}


export default App;
